@import '~antd/dist/antd.css';

.App {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  background-image: url(./header-bg-right.svg);
  background-repeat: no-repeat;
  background-position: 100% 0;
  min-height: 100vh;
  /* font-size: calc(10px + 2vmin); */
